<template>
	<div id="router">
		<header-bar :showGold="true"></header-bar>
		<team-intro></team-intro>
		<members></members>
		<social-media></social-media>
		<events></events>
		<footer-div></footer-div>
	</div>
</template>

<script>
import HeaderBar from '../components/Header'
import TeamIntro from '../components/home/TeamIntro'
import Members from '../components/home/Members'
import SocialMedia from '../components/home/SocialMedia'
import Events from '../components/home/Events'
import FooterDiv from '../components/Footer'

export default {
	name: 'home',
	components: {
		HeaderBar,
		TeamIntro,
		Members,
		SocialMedia,
		Events,
		FooterDiv
	},
	methods: {
	}
}
</script>

<style lang="scss">
@import '../variables.scss';
@font-face {
  font-family: 'ratio';
  src: url('../assets/fonts/ratio.woff2') format('woff2'),
       url('../assets/fonts/ratio.woff') format('woff'),
       url('../assets/fonts/ratio.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
.yellow{
	color: $yellow-dark;
}
a .yellow:hover{
	color: $yellow-light;
}
#app {
	font-family: $font-stack;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: $font-white;
	margin: 0;
	letter-spacing: 0.15em;
}
.parallax {
	background-image: url('../assets/welcomeBackground.png');
	background-attachment: fixed;
	background-repeat: no-repeat;
}
p,a{
	color: $font-white;
	text-decoration: none;
}
h1, h2, h3{
	font-weight: lighter;
}
</style>


