<template>
	<div id="nav-wrapper">
		<div id="gold-header" v-show="showGold">
			<div class="logo-wrap">
				<img class="gold-logo" src="../assets/Logotop1.png"/>
				<span>TEAMCATALYST.TV</span>
			</div>
			<div class="social">
				<social-media-icons :iconColor="'#5a5c64'"></social-media-icons>
			</div>
		</div>
		<nav id="nav-bar">
			<div class="stream-social" v-show="!showGold">
				<social-media-icons :iconColor="'#5a5c64'"></social-media-icons>
			</div>
			<div class='logo'>
			<!-- ADD LINK THAT TRIGGERS ELEVATOR JS OR RETURNS TO THE HOME PAGE -->
				<a href="./#/"><img src="../assets/Logoweb1.png" width="275px"></a>
			</div>
			<ul class="menu">
				<a href="./#/streams"><li class="link">STREAMS</li></a>
				<a href="./#/news/"><li class="link">NEWS</li></a>
				<a href="https://www.twitch.tv/team/catalyst/"><li class="link">TWITCH</li></a>
				<a href="./#/about"><li class="link">ABOUT US</li></a>
			</ul>
		</nav>
	</div>
</template>

<script>
import SocialMediaIcons from '../components/SocialMediaIcons'

export default {
	name: 'header-bar',
	props: ['showGold'],
	components: {SocialMediaIcons}
}
window.onscroll = function () {
	var scrollTop = (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop
	if (scrollTop < 400) {
		document.getElementById('gold-header').style['margin-bottom'] = '0'
		document.getElementById('nav-bar').style.position = 'initial'
	} else if (scrollTop >= 400 && scrollTop < 600) {
		document.getElementById('nav-bar').style.top = -100
	} else {
		document.getElementById('nav-bar').style.position = 'fixed'
		document.getElementById('gold-header').style['margin-bottom'] = '70px'
		document.getElementById('nav-bar').style.top = 0
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
a{
	text-decoration: none;
}
.link{
	display: inline-block;
	cursor: pointer;
	padding: 10px;
	line-height: 52px;
	width: 85px;
	cursor: pointer;
	color: $yellow-dark;
	transition: all .2s ease-in-out;
}
.link:hover{
	background-color: $yellow-dark;
	color: $gray-dark;
}
.link:active{
	background-color: $yellow-light;
	color: $gray-dark;
}
#nav-bar{
	position: initial;
	text-align: center;
	transition: all .5s ease-in-out;
	width: 100%;
	background-color: $gray-dark;
	top: -100;
}
#gold-header{
	background-color: $yellow-dark;
	background-image: url('../assets/Topbar1.png');
	position:relative;
	height: 100px;
}
.logo-wrap{
	color: $gray-light;
	display: inline-block;
	position: absolute;
	left: 5%;
	top: 12%;
	font-weight: bolder;
	font-size: 18px;
}
.social{
	display: inline-block;
	position: absolute;
	right: 5%;
	top: 10%;
}
.stream-social{
	display: inline-block;
	position: absolute;
	left: 3%;
}
.gold-logo{
	width:75px;
	vertical-align: middle;
}
.logo{
	display: inline-block;
	vertical-align: middle;
	margin: 10px auto;
}
.menu{
	display: inline-block;
	position: absolute;
	padding-right: 3%;
	right:0;
	font-size: 14px;
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
	-webkit-padding-start: 0;
}
@media screen and (max-width: 80.000em){ /*960px*/
	.link{
		width: auto;
	}
	#nav-bar{
		text-align: left;
	}
	.logo{
		padding-left: 3%;
	}
}
</style>


